<template>
  <div class="wrapper">
    <el-card shadow="hover" class="login">
      <el-input v-model="userId" placeholder="请输入用户id"></el-input>
      <el-input v-model="roomId" placeholder="请输入房间id"></el-input>

      <br>
      <el-button style="margin-top: 20px" @click="login" type="primary">进入直播间</el-button>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "login",

  data() {
    return {
      userId: null,
      roomId: null,
      role: null,
    }
  },

  methods: {
    login() {
      if (this.userId && this.roomId) {
        this.$router.push({path: '/living'});
        this.$store.commit('change_userId', this.userId);
        this.$store.commit('change_roomId', this.roomId);
      }
    },
  },

}
</script>

<style lang="scss" scoped>

.wrapper {
  // 登录卡片
  .login {
    width: 400px;
    height: 300px;
    position: fixed;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .el-input {
      margin: 20px auto;
    }
  }
}

</style>
