import Vue from 'vue'
import VueRouter from 'vue-router'
import login from "../views/login";
import living from "../views/living";

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    name:'login',
    component: login
  },
  {
    path:'/login',
    name:'login',
    component: login
  },
  {
    path:'/living',
    name:'living',
    component: living
  },


]

const router = new VueRouter({
  routes
})

export default router
