import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userId: null,
    roomId: null,
  },
  mutations: {
    change_userId(state,uid){
      state.userId = uid;
    },

    change_roomId(state,rid){
      state.roomId = rid;
    },


  },
  actions: {
  },
  modules: {
  }
})
