<template>
  <div class="wrapper">
    <el-button type="primary" @click="joinRoom">进入房间</el-button>
    <el-button type="primary" @click="leaveRoom">退出房间</el-button>

    <!--选择模式-->
    <div class="change-mode">
      <el-radio-group v-model="mode">
        <el-radio-button label="living">直播模式</el-radio-button>
        <el-radio-button label="videoPhone">通话模式</el-radio-button>
        <el-radio-button label="shareScreen">屏幕分享模式</el-radio-button>
      </el-radio-group>
    </div>

    <!--切换角色-->
    <div class="change-role" v-if="mode==='living'">
      <el-radio-group v-model="role">
        <el-radio-button label="anchor" @click="changeRole">主播</el-radio-button>
        <el-radio-button label="audience" @click="changeRole">观众</el-radio-button>
      </el-radio-group>
    </div>

    <div id="local"></div>
    <div id="remote"></div>

  </div>
</template>

<script>
import TRTC from 'trtc-js-sdk';
import {genTestUserSig} from "../../public/debug/GenerateTestUserSig";

export default {
  name: "living",

  data() {
    return {
      client: null,
      option: genTestUserSig(this.$store.state.userId),
      userId: this.$store.state.userId,
      roomId: this.$store.state.roomId,
      role: 'audience',
      mode: 'living',
    }
  },

  methods: {

    async joinRoom() {
      // 初始化客户端
      this.client = TRTC.createClient({
        mode: this.mode,
        sdkAppId: this.option.sdkAppID,
        userId: this.userId,
        userSig: this.option.userSig,
        useStringRoomId: true,
      });

      this.client.on('stream-added', event => {
        const remoteStream = event.stream;
        console.log('远端流增加: ' + remoteStream.getId());

        //订阅远端流
        this.client.subscribe(remoteStream);
      });
      this.client.on('stream-subscribed', event => {
        const remoteStream = event.stream;
        console.log('远端流订阅成功：' + remoteStream.getId());

        // 播放远端流
        remoteStream.play('remote');
      });

      // 进房
      await this.client.join({
        roomId: this.roomId,
        role: this.role,
      })

      if (!(this.role === 'audience' && this.mode === 'living')){
        // 创建本地音视频流
        // eslint-disable-next-line no-unused-vars
        const localStream = TRTC.createStream({userId: this.userId, audio: true, video: true});
        await localStream.initialize();
        // 推流
        this.client
            .publish(localStream)
            .catch(error => {
              console.error('本地流发布失败 ' + error);
            })
            .then(() => {
              console.log('本地流发布成功');
              localStream.play('local')
            });
      }


    },

    //直播模式切换角色
    changeRole() {
      this.client
          .switchRole(this.role)
          .catch(error => {
            console.error('角色切换失败 ' + error);
          })
          .then(() => {
            // 角色切换成功，现在是主播角色
          });
    },

    // 离开房间
    leaveRoom() {
      this.client.leave().catch(error => {
        console.log(error);
      }).then(() => {
        console.log('退房成功');
      })
    },

  },

  mounted() {
  },

}
</script>

<style scoped>

#local {
  width: 100px;
  height: 100px;
}

#remote {
  width: 100px;
  height: 100px;
}

</style>
